import React, { Component } from "react"
import Link from "gatsby-link"
import Layout from "../components/layout"
import Img from "gatsby-image"

const NavLink = props => {
  if (!props.test) {
    return <Link to={props.url}>{props.text}</Link>
  } else {
    return <span>{props.text}</span>
  }
}

const IndexPage = ({ pageContext }) => {
  const { group, index, first, last, pageCount } = pageContext
  const previousUrl = index - 1 === 1 ? "" : (index - 1).toString()
  const nextUrl = (index + 1).toString()

  const filteredGroup = group.filter(
    ({ node }) => node.categories[0].name != "Project"
  )
  //console.log(filteredGroup)
  //filter posts based on category - in this case "Article"
  return (
    <Layout>
      <h4>{pageCount} Pages</h4>

      {filteredGroup.map(({ node }) => (
        <div key={node.slug} className={"post"}>
          {node.featured_media && (
            <>
              <Img
                resolutions={
                  node.featured_media.localFile.childImageSharp.resolutions
                }
              />
            </>
          )}

          <Link to={`post/${node.slug}`}>
            <h3>{node.title}</h3>
          </Link>
          <div
            className={"post-content"}
            dangerouslySetInnerHTML={{ __html: node.excerpt }}
          />
          {node.date}
        </div>
      ))}

      {index === 1 && pageCount !== 1 && (
        <div className="nextLink">
          <NavLink
            test={last}
            url={`/posts/${nextUrl}`}
            text="Go to Next Page"
          />
        </div>
      )}
      {index !== 1 && pageCount !== 1 && (
        <>
          <div className="previousLink">
            <NavLink
              test={first}
              url={`/posts/${previousUrl}`}
              text="Go to Previous Page"
            />
          </div>
          <div className="nextLink">
            <NavLink
              test={last}
              url={`/posts/${nextUrl}`}
              text="Go to Next Page"
            />
          </div>
        </>
      )}
      {index === pageCount && pageCount > 1 && (
        <div className="previousLink">
          <NavLink
            test={first}
            url={`/posts/${previousUrl}`}
            text="Go to Previous Page"
          />
        </div>
      )}
    </Layout>
  )
}

export default IndexPage
